/* Basic resets and font */
body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5; /* Default line height */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Base App Container */
.app-container {
  min-height: 100vh;
  background: linear-gradient(to bottom right, #1a202c, #2d3748); /* Darker, sleek gradient */
  color: white;
  padding: 1rem; /* p-4 */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Align to top, not center */
}

/* Header Styles */
.app-header {
  width: 100%;
  max-width: 32rem; /* max-w-lg */
  margin-bottom: 2rem; /* mb-8 */
  text-align: center;
  padding-top: 2rem; /* Add some padding at the top */
}

.app-title {
  font-size: 3rem; /* Larger text */
  font-weight: 800; /* font-extrabold */
  margin-bottom: 0.5rem; /* mb-2 */
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4); /* Stronger text-shadow */
  color: #63b3ed; /* A vibrant blue for the title */
}

.app-description {
  color: #a0aec0; /* Lighter gray for description */
  font-size: 0.875rem; /* text-sm */
}

.user-id-text {
  font-family: monospace; /* font-mono */
  font-size: 0.75rem; /* text-xs */
  word-break: break-all; /* break-all */
  background-color: rgba(66, 153, 225, 0.2); /* A subtle blue background */
  border-radius: 0.25rem; /* rounded */
  padding: 0 0.25rem; /* px-1 */
  color: #90cdf4; /* Lighter blue text for ID */
}


/* Card Container Styles */
.card-container {
  width: 100%;
  max-width: 28rem; /* max-w-md */
  background-color: rgba(255, 255, 255, 0.08); /* Semi-transparent white for cards */
  backdrop-filter: blur(10px); /* Frosted glass effect */
  border: 1px solid rgba(255, 255, 255, 0.15); /* Subtle border */
  padding: 1.5rem; /* p-6 */
  border-radius: 0.75rem; /* rounded-xl */
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3); /* Stronger shadow */
  display: flex;
  flex-direction: column;
  gap: 1.5rem; /* space-y-6 */
  margin-bottom: 2rem; /* mb-8 */
  position: relative; /* for absolute positioning of suggestions */
}

/* Input and Button Group */
.input-button-group {
  display: flex;
  flex-direction: column;
  gap: 1rem; /* gap-4 */
  position: relative; /* for absolute positioning of suggestions */
}

/* Responsive adjustment for input/button group */
@media (min-width: 640px) { /* sm breakpoint */
  .input-button-group {
    flex-direction: row;
  }
}

/* Input Field Styles */
.text-input {
  flex-grow: 1; /* flex-grow */
  padding: 0.75rem; /* p-3 */
  border-radius: 0.5rem; /* rounded-lg */
  border: 1px solid #4a5568; /* darker border */
  background-color: #2d3748; /* dark input background */
  outline: none; /* Remove default outline */
  font-size: 1.125rem; /* text-lg */
  color: #e2e8f0; /* light text color */
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1); /* subtle inner shadow */
  transition: border-color 0.2s, box-shadow 0.2s; /* Add transition for focus */
}

.text-input::placeholder {
  color: #a0aec0; /* Lighter placeholder text */
}

.text-input:focus {
  border-color: #63b3ed; /* Focus border blue */
  box-shadow: 0 0 0 3px rgba(99, 179, 237, 0.5); /* Blue focus ring */
  background-color: #4a5568; /* Slightly lighter on focus */
}

/* Button Styles */
.action-button {
  background-color: #4299e1; /* primary blue */
  color: white;
  font-weight: 700; /* font-bold */
  padding: 0.75rem 1.5rem; /* py-3 px-6 */
  border-radius: 0.5rem; /* rounded-lg */
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2); /* softer shadow */
  transition: all 0.3s ease-in-out; /* transform transition duration-300 ease-in-out */
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem; /* gap-2 */
}

.action-button:hover {
  background-color: #3182ce; /* darker blue on hover */
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.action-button:active {
  transform: scale(0.96); /* slightly less scale */
}

.action-button:focus {
  outline: none; /* focus:outline-none */
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.5); /* Blue focus ring */
}

.action-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  box-shadow: none;
}

/* Green Add to Portfolio Button */
.add-portfolio-button {
  background-color: #48bb78; /* bg-green-500 */
}

.add-portfolio-button:hover {
  background-color: #38a169; /* hover:bg-green-600 */
}

.add-portfolio-button:focus {
  box-shadow: 0 0 0 3px rgba(72, 187, 120, 0.5);
}

/* Red Remove Button */
.remove-button {
  background-color: #f56565; /* bg-red-400 */
  padding: 0.5rem 1rem; /* py-2 px-4 */
  font-size: 0.875rem; /* text-sm */
}

.remove-button:hover {
  background-color: #e53e3e; /* hover:bg-red-500 */
}

.remove-button:focus {
  box-shadow: 0 0 0 3px rgba(245, 101, 101, 0.5);
}

/* Loading Spinner */
.spinner {
  animation: spin 1s linear infinite;
  height: 1.25rem; /* h-5 */
  width: 1.25rem; /* w-5 */
  color: white; /* text-white */
  display: inline-block;
  margin-right: 0.5rem; /* mr-2 */
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Error Message Styles */
.error-message {
  color: #fbd38d; /* light orange text */
  background-color: rgba(235, 110, 75, 0.15); /* subtle reddish background */
  border: 1px solid #fc8181; /* red border */
  padding: 0.75rem; /* p-3 */
  border-radius: 0.5rem; /* rounded-lg */
  text-align: center;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06); /* shadow-md */
}

/* Stock Data Display Card */
.stock-data-card {
  background: rgba(255, 255, 255, 0.08); /* Same as card-container for consistency */
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  padding: 1.5rem; /* p-6 */
  border-radius: 0.75rem; /* rounded-xl */
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2); /* shadow-lg */
  color: #e2e8f0; /* light text color */
  display: flex;
  flex-direction: column;
  gap: 1rem; /* space-y-4 */
}

.stock-data-card h2 {
  font-size: 1.875rem; /* text-3xl */
  font-weight: 700; /* font-bold */
  color: #63b3ed; /* vibrant blue */
  margin-bottom: 0.5rem; /* mb-2 */
}

.stock-data-card p {
  font-size: 1.125rem; /* text-lg */
}

.stock-data-card strong {
  font-weight: 700; /* font-bold */
}

.stock-price {
  font-size: 1.5rem; /* text-2xl */
  font-weight: 800; /* font-extrabold */
}

.stock-price .value {
  color: #48bb78; /* green */
}

.stock-change {
  font-size: 1.25rem; /* text-xl */
  font-weight: 600; /* font-semibold */
}

.stock-change.positive {
  color: #48bb78; /* green */
}

.stock-change.negative {
  color: #f56565; /* red */
}

.stock-details-grid {
  display: grid;
  grid-template-columns: 1fr; /* grid-cols-1 */
  gap: 0.75rem; /* gap-3 */
  font-size: 1.125rem; /* text-lg */
}

@media (min-width: 640px) { /* sm breakpoint */
  .stock-details-grid {
    grid-template-columns: repeat(2, 1fr); /* sm:grid-cols-2 */
  }
}

.data-note {
  font-size: 0.875rem; /* text-sm */
  color: #a0aec0; /* text-gray-400 */
  font-style: italic; /* italic */
  margin-top: 1rem; /* mt-4 */
}

/* Suggestions List */
.suggestions-list {
  position: absolute;
  z-index: 10;
  left: 0;
  right: 0;
  width: 100%;
  background-color: #2d3748; /* dark background for suggestions */
  border: 1px solid #4a5568; /* dark border */
  border-radius: 0.5rem; /* rounded-lg */
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2); /* shadow-lg */
  top: calc(100% + 0.5rem); /* top-[calc(100%+0.5rem)] */
  max-height: 15rem; /* max-h-60 */
  overflow-y: auto;
  color: #e2e8f0; /* light text */
}

.suggestions-list-item {
  padding: 0.75rem; /* p-3 */
  cursor: pointer;
  border-bottom: 1px solid #4a5568; /* border-b darker */
  transition: background-color 0.15s ease-in-out;
}

.suggestions-list-item:last-child {
  border-bottom: none; /* last:border-b-0 */
}

.suggestions-list-item:hover {
  background-color: #4a5568; /* lighter background on hover */
}

.suggestions-list-item span {
  font-weight: 600; /* font-semibold */
}

/* Portfolio Section */
.portfolio-section {
  width: 100%;
  max-width: 28rem; /* max-w-md */
  background: rgba(255, 255, 255, 0.08); /* Same as card-container */
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  padding: 1.5rem; /* p-6 */
  border-radius: 0.75rem; /* rounded-xl */
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3); /* shadow-2xl */
  display: flex;
  flex-direction: column;
  gap: 1rem; /* space-y-4 */
  margin-top: 2rem; /* mt-8 */
  margin-bottom: 2rem; /* Added margin-bottom to separate from bottom of screen */
}

.portfolio-section h2 {
  font-size: 1.875rem; /* text-3xl */
  font-weight: 700; /* font-bold */
  color: #63b3ed; /* vibrant blue */
  margin-bottom: 1rem; /* mb-4 */
  text-align: center;
}

.portfolio-grid {
  display: grid;
  grid-template-columns: 1fr; /* grid-cols-1 */
  gap: 1rem; /* gap-4 */
}

.portfolio-item {
  background-color: rgba(255, 255, 255, 0.05); /* slightly lighter background for items */
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 1rem; /* p-4 */
  border-radius: 0.5rem; /* rounded-lg */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.15); /* subtle shadow */
  color: #e2e8f0; /* light text */
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: grab; /* cursor-grab for draggable items */
  transition: all 0.2s ease-in-out;
}

.portfolio-item:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

.portfolio-item.dragging {
  opacity: 0.6; /* opacity-50 */
  border: 2px solid #63b3ed; /* border-blue-400 for dragging */
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}

.portfolio-item h3 {
  font-size: 1.25rem; /* text-xl */
  font-weight: 700; /* font-bold */
  color: #90cdf4; /* lighter blue */
}

.portfolio-item .company-name {
  font-size: 0.875rem; /* text-sm */
  color: #a0aec0; /* light gray */
}

.portfolio-item .price {
  font-size: 1.125rem; /* text-lg */
  font-weight: 600; /* font-semibold */
  margin-top: 0.25rem; /* mt-1 */
}

.portfolio-item .price .value {
  color: #48bb78; /* green */
}

.portfolio-item .change-text {
  font-size: 1rem; /* text-md */
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.6); /* darker overlay */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
  padding: 1rem; /* p-4 */
}

.modal-content {
  background-color: #2d3748; /* dark background for modal */
  padding: 1.5rem; /* p-6 */
  border-radius: 0.5rem; /* rounded-lg */
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.5); /* stronger shadow */
  color: #e2e8f0; /* light text */
  width: 100%;
  max-width: 24rem; /* max-w-sm */
  display: flex;
  flex-direction: column;
  gap: 1rem; /* space-y-4 */
  border: 1px solid #4a5568; /* subtle border */
}

.modal-content h3 {
  font-size: 1.25rem; /* text-xl */
  font-weight: 700; /* font-bold */
  color: #63b3ed; /* vibrant blue */
}

.modal-input {
  width: 100%;
  padding: 0.75rem; /* p-3 */
  border-radius: 0.5rem; /* rounded-lg */
  border: 1px solid #4a5568; /* dark border */
  background-color: #1a202c; /* even darker input background */
  outline: none;
  font-size: 1.125rem; /* text-lg */
  color: #e2e8f0;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
  transition: border-color 0.2s, box-shadow 0.2s;
}

.modal-input:focus {
  border-color: #63b3ed;
  box-shadow: 0 0 0 3px rgba(99, 179, 237, 0.5);
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem; /* gap-3 */
}

.modal-cancel-button {
  background-color: #a0aec0; /* gray */
  color: #1a202c; /* dark text */
}

.modal-cancel-button:hover {
  background-color: #718096; /* darker gray */
}

.modal-create-button {
  background-color: #4299e1; /* primary blue */
  color: white;
}

.modal-create-button:hover {
  background-color: #3182ce; /* darker blue */
}

/* SVG Icon Helper Class - For consistency */
.icon-h5-w5 {
  height: 1.25rem; /* h-5 */
  width: 1.25rem; /* w-5 */
}
